.chatPage{
    background-color: hwb(60 79% 19%);
    /* background-image: url(../../images/saurabh.jpg); */
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.chatContainer{
    border-right-color: black;
    border-radius: 5px;
    border-right-width: 10px;
    /* background-image: url(../../images/saurabh.jpg); */
    background-color: hsl(0, 0%, 100%);
    height: 60%;
    width: 50%;
}

.header{
    background-color: #418bed;
    height:15%;
    display: flex;
    align-items: center;
    justify-content:space-between ;
}

.header>h2{
    color: white;
    margin: 2.5vmax;
}

.my{
    block-size: 10px;
    margin: 2.5vmax;
    transition: all 0.3s;
    cursor: pointer;
}

.header img{
    block-size:40px;
    size-adjust: cover;
    margin: 1vmax;
    transition: all 0.3s;
    cursor: pointer;
}


.header img:hover{
    transform: scale(1.2);
}

.chatBox{
    /* border: 0.2vmax solid black; */
    height: 70%;
    box-sizing: border-box;
    /* overflow-y: auto ; */
}

.InputBox{
    height: 15%;
    box-sizing: border-box;
    display: flex;
}

#chatInput{
    width: 80%;
    border : none;
    padding: 2vmax;
    box-sizing: border-box;
    background-color: white;
    outline: none ;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 1.2vmax;
    border-top: 0.2vmax solid #2f2e2e25;
}

.sendBtn{
    background-color: #418bed;
    color: white;
    border: none;
    width: 21%;
    transition: all 0.3s;
    cursor: pointer;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-size: 12vmax;
}
.sendBtn>img{
    width:2.6vmax;
    /* filter: brightness(100) invert(); */
    height: 40px;
    margin-bottom: 130px;
}

.sendBtn:hover{
    background-color: hsl(240, 99%, 49%);
    
}

.sendBtn:hover img{
    transform: translateX(10px);
}